  }
}

@media (max-width: 767px) {
  .nav--closed .nav__list {
    display: none;
  }
}

.nav--closed .nav__toggle::before,
.nav--opened .nav__toggle::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 1px;
  left: 0;
  background-color: rgba(248, 253, 255, 0.7);
}

.nav--closed .nav__toggle::after {
  position: absolute;
  content: "";
  width: 1px;
  height: 16px;
  top: 0;
  background-color: rgba(248, 253, 255, 0.7);
}

